.NotSignedIn_nav{
    width: 100%;
    min-height: 60px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    position: fixed;
    background-color: #fff;
    z-index: 40;
}
.NotSignedIn_left{
    cursor: pointer;
}
.NotSignedIn_EditorLink{
    padding: 8px;
    max-width: 100px;
    margin-right: 1rem;
    font-family: sans-serif;
    text-decoration: none;
    font-size: 20px;
    color: hsl(225, 14%, 20%);
    transition: 0.2s all;
}
.NotSignedIn_EditorLink:hover{
    color:rgba(59, 58, 58, 0.692)
}
.NotSignedIn_right{
    width: 80%;
    display: flex;
    justify-content:flex-end ;
    height: 100%;
}

.NotSignedIn_home-nav h2{
    color: rgb(59, 58, 58);
    margin-left: 1rem;
}
.NotSignedIn_profile{
    background-color: pink;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    cursor: pointer;


}
.NotSignedIn_profile img{
    width:100%;
    height: 100%;
    clip-path: circle(30px at 50% 50%);
}

.svg{
    width:150px;
}