  justify-content: space-around;
  max-width: 1400px;
  width: 100%;
  margin: auto;
  margin-bottom: 4vh;
  max-height: 42vh;
  border-radius: 4px;
  
  background-color: rgb(255, 255, 255);
}

#explorebutton {
  background-color: black;
  color: white;
  padding: 10px 24px;
  border-radius: 6px;
  -webkit-transform: translateY(20px);
          transform: translateY(20px);
  font-weight: 600;
  cursor: pointer;
  transition: 0.4s all ease-out;
  font-size: 18px;
  border: none;
}

#explorebutton:hover {
  background-color: rgb(255, 255, 255);
  color: black;
  border: none;
}
#explorebutton:active{
  background-color: rgb(235, 235, 235);
}

.Explore_Section_textarea {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
    max-width: 600px;
    width: 45%;
    height: 80%;
    padding-left: 6vw;
}

.Explore_Section_textarea h1 {
  text-align: center;
  color: #f7032e;
  font-size: 36px;
  -webkit-transform: translateY(-10px);
          transform: translateY(-10px);
}

.Explore_Section_textarea p {
  font-size: 20px;
  font-weight: 400;
  color: rgb(24, 44, 58);
  font-family: sans-serif;

}

.Explore_Section_imagearea {
  width: 50%;
    object-fit: contain ;
    max-height: 400px;
}


.Explore_Section_imagearea img {
    transition: all 0.7s ease 0s;
    /* filter: drop-shadow(5px 5px 5px rgb(77, 75, 75)); */
    width: 600px;
    height: 400px;
}

.imagearea img:hover {
  -webkit-transform: scale(1.04);
          transform: scale(1.04);
  /* filter: drop-shadow(5px 5px 5px #222); */
}

@media screen and (max-width: 1151px) {
    .Explore_Section_textarea h1{
